@import 'src/assets/style/variables';
@import 'src/assets/style/mixin';


.L-home-container {
  width       : 100%;
  height      : 100vh;
  padding-top : 200px;

  .L-home-wrapper {

	.L-img-cont {
	  max-width     : 420px;
	  width         : 100%;
	  min-height    : 500px;
	  background    : transparent;
	  box-shadow    : 0 15px 20px rgba(0, 0, 0, 0.3);
	  border-radius : 30px;
	  overflow      : hidden;
	  border        : 10px solid rgba(255, 255, 255, 0.1);

	  .img {
		width  : 450px;
		height : 500px;
		margin : 0 auto;
	  }

	}
  }

  .L-introducing-container {
	width   : max-content;
	margin  : 20px auto;
	padding : 15px 10px;


	h1 {
	  font-size   : $font-size-40;
	  color       : $white;
	  text-shadow : 2px 0 black, -2px 0 #000, 0 2px #000, 0 -2px #000,
	  1px 1px #000, -1px -1px #000, 1px -1px #000, -1px 1px #000;
	}

	h2 {
	  margin-top  : 20px;
	  font-size   : $font-size-36;
	  color       : $white;
	  text-shadow : 2px 0 black, -2px 0 #000, 0 2px #000, 0 -2px #000,
	  1px 1px #000, -1px -1px #000, 1px -1px #000, -1px 1px #000;
	}
  }

}